html,
body {
  height: 95%;
  margin: 0;
}

body {
  font-family: sans-serif;
  background-color: #f4f4f4;
  color: #333;
  display: flex;
  flex-direction: column;
  padding: 20px;
  padding-bottom: 30px;
}

h1 {
  color: #0056b3;
  text-align: center;
  margin-top: 0;
  margin-bottom: 10px;
}

.back-link {
  text-align: center;
  margin-bottom: 20px;
}

.back-link a {
  color: #007bff;
  text-decoration: none;
}

.back-link a:hover {
  text-decoration: underline;
}

.container {
  display: flex;
  gap: 20px;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
}

.option-section {
  margin-bottom: 20px;
  padding: 15px;
  border: 1px solid #eee;
  border-radius: 8px;
  background-color: #f9f9f9;
  width: 600px;
  box-sizing: border-box;
  flex: none;
}

.option-section h3 {
  color: #0056b3;
  margin-top: 0;
}

.option-section pre {
  background-color: #eef;
  padding: 10px;
  border-radius: 4px;
  overflow-x: auto;
}

.image-container {
  display: flex;
  gap: 20px;
  margin-top: 10px;
}

.image-container div {
  text-align: center;
}

.render-example {
  border: 1px solid #ccc;
  max-width: 100%;
  height: auto;
}
